import request, { post, get } from '@/utils/request'
import { ouchnCustom } from '../../service'

const urlGetUserOrderList = ouchnCustom + '/web/api/ocOrder/orderList'
const urlCreateOrder = ouchnCustom + '/web/api/ocOrder/addOcOrder'
const urlCreateSubjectOrder = ouchnCustom + '/web/api/ocOrder/addOcOrderSubject'
const urlGetOrderDetail = ouchnCustom + '/web/api/ocOrder/orderChildList'
const urlDeleteOrderRecordById = ouchnCustom + '/web/api/ocOrder/deleteById'
const urlGetTotalGroup = ouchnCustom + '/web/api/ocOrder/orderCountByStatus'
const urlCancelOrder = ouchnCustom + '/web/api/ocOrder/updateOrderStatus'
const urlAfterPay = ouchnCustom + '/web/api/ocOrder/addOrderInMyResource'
const urlCheckIsCreated = ouchnCustom + '/web/api/ocOrder/orderCompareByUserId'
const IntegralRecord = ouchnCustom + '/web/IntegralRecord/list'
const IntegralRecordCount = ouchnCustom + '/web/IntegralRecord/count'

// 
export const apiGetUserOrderList = (params) => get(urlGetUserOrderList, params)

// 
export const apiCreateOrder = (data) => post(urlCreateOrder, data)

// 
export const apiCreateSubjectOrder = (data) => post(urlCreateSubjectOrder, data)

// 
// export const apiCancelOrder = (data) => post(urlCancelOrder, data)
export const apiCancelOrder = (data) => post(`${urlCancelOrder}?id=${data.id}`)

// 
export const apiAfterPay = (data) => post(`${urlAfterPay}?id=${data.id}`)

// 
export const apiGetOrderDetail = (params) => get(urlGetOrderDetail, params)

// total
export const apiGetTotalGroup = (params) => get(urlGetTotalGroup, params)

// 
export const apiCheckIsCreated = (params) => get(urlCheckIsCreated, params)

// 
export const apiDeleteOrderRecordById = (params) => {
  return request({
    url: urlDeleteOrderRecordById,
    method: 'delete',
    params,
  })
}
// 
export const Integral = (params) => {
  return request({
    url: IntegralRecord,
    method: 'get',
    params,
  })
}
// 
export const IntegralCount = (params) => {
  return request({
    url: IntegralRecordCount,
    method: 'get',
    params,
  })
}